a {
  text-decoration: none;
  color: #E0ECFF;
}
.trade-claim {
  width: 1142px;
  margin: 0 auto;
  margin-bottom: 51px;
  border: 1px solid #203B60;
  background: rgba(85, 119, 253, 0.1);
  padding-left: 38px;
  padding-top: 17px;
  height: 192px;
  color: #FFFFFF;
}
.trade-claim .rewards-claim {
  font-weight: normal;
  font-size: 24px;
}
.trade-claim .rewards-info {
  height: 64px;
  margin-top: 47px;
}
.trade-claim .rewards-info .md-rewards-info {
  display: none;
}
.trade-claim .rewards-info .lg-rewards-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.trade-claim .rewards-info .my-rewards,
.trade-claim .rewards-info .claimed-deri,
.trade-claim .rewards-info .locked-deri,
.trade-claim .rewards-info .claimable-deri,
.trade-claim .rewards-info .cliam-button {
  flex: 1;
  margin-left: 54px;
}
.trade-claim .rewards-info .my-rewards,
.trade-claim .rewards-info .claimed-deri,
.trade-claim .rewards-info .locked-deri {
  border-right: 1px solid #203B60;
}
.trade-claim .rewards-info .cliam-button {
  padding-right: 42px;
  display: flex;
  align-items: center;
}
.trade-claim .rewards-info .my-rewards {
  margin-left: 0;
}
.trade-claim .rewards-info .claim-title {
  font-weight: 500;
  font-size: 14px;
  height: 22px;
  color: #93A1B8;
}
.trade-claim .rewards-info .claim-num {
  font-weight: 500;
  font-size: 24px;
  height: 38px;
  white-space: nowrap;
  padding-right: 20px;
}
@media only screen and (max-width: 759px) {
  .trade-claim {
    width: 327px;
    height: 332px;
    padding-left: 24px;
  }
  .trade-claim .rewards-info {
    margin-top: 30px;
  }
  .trade-claim .rewards-info .lg-rewards-info {
    flex-wrap: wrap;
  }
  .trade-claim .rewards-info .lg-rewards-info .my-rewards,
  .trade-claim .rewards-info .lg-rewards-info .claimed-deri,
  .trade-claim .rewards-info .lg-rewards-info .locked-deri,
  .trade-claim .rewards-info .lg-rewards-info .claimable-deri,
  .trade-claim .rewards-info .lg-rewards-info .cliam-button {
    margin-left: 0;
    margin-top: 20px;
    margin-left: 20px;
  }
}
@media only screen and (min-width: 760px) and (max-width: 1139px) {
  .trade-claim {
    width: 725px;
    height: 219px;
  }
  .trade-claim .rewards-info {
    margin-top: 30px;
  }
  .trade-claim .rewards-info .lg-rewards-info {
    display: none;
  }
  .trade-claim .rewards-info .md-rewards-info {
    display: block;
  }
  .trade-claim .rewards-info .md-rewards-info .top-rewards-info {
    display: flex;
    justify-content: space-between;
  }
  .trade-claim .rewards-info .md-rewards-info .top-rewards-info .my-rewards,
  .trade-claim .rewards-info .md-rewards-info .top-rewards-info .cliam-button {
    flex: 0;
    border: 0;
  }
  .trade-claim .rewards-info .md-rewards-info .bottom-rewards-info {
    display: flex;
    margin-top: 10px;
  }
  .trade-claim .rewards-info .md-rewards-info .bottom-rewards-info .claimed-deri {
    margin-left: 0;
  }
}
@media only screen and (min-width: 1140px) and (max-width: 1270px) {
  .trade-claim {
    width: 1041px;
  }
}
